import React, { useState, useRef, useEffect } from "react";
import {Navbar, Nav } from 'react-bootstrap';
import { AnchorLink } from "gatsby-plugin-anchor-links";
import '../../styles/all.css';
import './navbar.css';


const SiteNavbar = () => {
  const [navActive, setNavActive] = useState(false)

  function showBackground() {
    setNavActive(!navActive)
  }

  const [navBackground, setNavBackground] = useState(false)
    const navRef = useRef()
    navRef.current = navBackground
    useEffect(() => {
      const handleScroll = () => {
        const show = window.scrollY > 50
        if (navRef.current !== show) {
          setNavBackground(show)
        }
      }
      document.addEventListener('scroll', handleScroll)
      return () => {
        document.removeEventListener('scroll', handleScroll)
      }
    }, [])

  return (
    <Navbar collapseOnSelect expand="lg" fixed="top" onToggle={showBackground} style={{ transition: '1s ease', backgroundColor: navBackground ? 'white' : 'transparent'}} className={navActive ? "navbar-active" : "navbar-inactive"}>
      <AnchorLink to="/" className="navLink px-1">JC</AnchorLink>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
      <Nav className="navInfo mx-auto">
        <AnchorLink to="/about/" className="navLink">O mnie</AnchorLink>
        <AnchorLink to="/training/" className="navLink">Trening</AnchorLink>
        <AnchorLink to="/info/" className="navLink stripped" stripHash>Kontakt</AnchorLink>
      </Nav>
      </Navbar.Collapse>
    </Navbar>
  )
}

export default SiteNavbar
